.App {
  padding: 1em;
}

h1 {
  margin: 0.5rem 0;
}

h2, h3 {
  margin-bottom: 0;
}

.Header label {
  display: block;
}

.Header label span {
  font-weight: bold;
}

.emojiButton {
  border: 0;
  background: 0;
  padding: 0;
  font-size: 1.3rem;
}

.emojiButton:active {
  opacity: 0.5;
}

.CardDeck {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.Card {
  border: 1px solid #666;
  box-shadow: 2px 2px 4px rgba(0,0,0,0.8);
  min-width: 230px;
  min-height: 230px;
  margin: 1em;
  padding: 1.2em;
  display: inline-flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-content: space-around;
  flex: 0 1 230px;
}

.Card p {
  margin: 0;
  font-size: 0.8em;
}

.Card span {
  font-size: 3rem;
  height: 1.2em;
}

.Card img {
  margin: 0.5em;
  max-width: 50px;
}

.Card--round {
  border-radius: 50%;
  padding: 2em;
  min-width: 250px;
  min-height: 250px;
}

.Card--large {
  min-width: 500px;
  min-height: 500px;
  flex-basis: 500px;
  padding: 2em;
}

.Card--large.Card--round {
  padding: 3em;
}

.Card--large span {
  font-size: 6em;
  margin: 0.1em;
}

.Card--large.Card--round span {
  margin: 0.05em;
}

.Card--large img {
  margin: 1.4em;
  max-width: 100px;
}

.Card--large.Card--round img {
  margin: 1.1em;
}


.Card--rotation :nth-child(6n + 1) {
  transform: rotate(135deg);
}

.Card--rotation :nth-child(6n + 2) {
  transform: rotate(-135deg);
}

.Card--rotation :nth-child(6n + 3) {
  transform: rotate(90deg);
}

.Card--rotation :nth-child(6n + 4) {
  transform: rotate(-90deg);
}

.Card--rotation :nth-child(6n + 5) {
  transform: rotate(45deg);
}

.Card--rotation :nth-child(6n + 6) {
  transform: rotate(-45deg);
}

@media screen and (max-width: 768px) {
  html {
    font-size: 16pt;
  }
  input {
    font-size: 1rem;
  }
  input[type="radio"],
  input[type="checkbox"]
  {
    width: 1rem;
    height: 1rem;
  }
  .emojiButton {
    padding: 0.25em;
  }
}

@media print {
  .Header {
    display: none;
  }
  .Card {
    margin: 0.5em;
    box-shadow: none;
    page-break-inside: avoid;
  }
}

.visually-hidden {
    position: absolute !important;
    height: 1px;
    width: 1px;
    overflow: hidden;
    clip: rect(1px, 1px, 1px, 1px);
}
